import VeHistogram from 'v-charts/lib/histogram'
export default {
  name: 'Chart',
  components: {
    VeHistogram
  },
  props: {
    tech: Array
  },
  data () {
    this.extend = {
      xAxis: {
        axisLabel: {
          rotate: 60
        }
      }
    }
    this.chartSettings = {
      // legendName: {
      //   '': ''
      // },
      metrics: [''],
      dimension: [''],
      max: ['100']
    }
    return {
      chartData: {
        columns: ['', ''],
        rows: [
          // { 'name': 'Jedi', '': '', '': 12.4 },
          // { 'name': 'Jedi', '': '', '': 23.2 },
          // { 'name': 'Jedi', '': '', '': 34.5 },
          // { 'name': 'Jedi', '': '', '': 99.7 },
          // { 'name': 'Jedi', '': '', '': 52.6 },
          // { 'name': 'Jedi', '': '', '': 35.5 },
          // { 'name': 'Jedi', '': '', '': 37.4 },
          // { 'name': 'Jedi', '': '', '': 42.4 },
          // { 'name': 'Jedi', '': '', '': 35.6 },
          // { 'name': 'Jedi', '': '', '': 35.6 }
        ]
      }
    }
  },
  watch: {
    tech () {
      this.chartData.rows = this.tech
    }
  }
}



